<!-- 这是加减按钮组件 -->
<template>
	<div class="carcontrol">
		<div class="car-decrease iconremove_circle_outline" v-show="food.count>0" @click="decNum"></div>
		<div class="car-count" v-show="food.count>0">{{food.count}}</div>
		<div class="car-increase iconadd_circle" @click="addNum"></div>
	</div>
</template>

<script>
	import Vue from 'vue'//使用Vue.set()方法应该引用的文件
	export default{
		name:'carcontrol',
		props:{
			food:{//在goods组件里面传过来的
				type:Object
			}
		},
		created(){
			// console.log(this.food);
		},
		methods:{
			addNum:function(){
				if (!event._constructed) {
			  			return;//防止better-scroll的event事件不一致
			  		}
			  		// console.log('click');
				if (!this.food.count) {
					Vue.set(this.food,'count',1);//因为food里面没有count属性，所以应该使用Vue.set()方法，可以给对象设置新属性，并赋初始值
				}else{
					this.food.count++;
				}
			},
			decNum:function(){
				if (!event._constructed) {
			  			return;//防止better-scroll的event事件不一致
			  		}
			  	if (this.food.count>0) {
			  		this.food.count--;
			  	}
			}
		}
	}
</script>

<style scoaped>
.carcontrol{
	font-size: 0;
}
.car-decrease{
	display: inline-block;
	font-size: 24px;
	line-height: 24px;
	color: rgb(0,160,220);
}
.car-count{
	display: inline-block;
	width: 24px;
	font-size: 10px;
	line-height: 24px;
	color:rgb(147,153,159);
	vertical-align: top;
	text-align: center;
}
.car-increase{
	display: inline-block;
	font-size: 24px;
	line-height: 24px;
	color: rgb(0,160,220);
}
</style>


