<!-- 这是购物车组件 -->
<template>
	<div class="shopcar">
		<div class="scontent" @click="toggleList">
			<div class="scontent-left">
				<div class="logo-wrapper">
					<div class="logo" :class="{'heighlight':totalCount>0}">
						<span class="iconshopping_cart" :class="{'textlight':totalCount>0}"></span>
					</div>
					<div class="num" v-show="totalCount>0">{{totalCount}}</div><!-- 购物车上面的数字统计，可以在里面使用函数 -->
				</div>
				<div class="sprice" :class="{'pricelight':totalPrice>0}">￥{{totalPrice}}</div>
				<div class="sdec">另需配送费￥{{deliveryPrice}}元</div>
			</div>
			<div class="scontent-right">
			<div class="pay" :class="payClass">{{payDec}}</div>
			</div>
		</div>
		<div class="shopcar-list" v-show="listShow" transition="fold-transition"><!-- 购物车点击详情页star -->
			<div class="list-header">
				<h1 class="list-title">购物车</h1>
				<span class="list-clear">清空</span>
			</div>
			<div class="list-content">
				<ul>
					<li class="list-food" v-for="food in selectFoods">
						<span class="list-name">{{food.name}}</span>
						<div class="list-price">
							<span>￥{{food.price*food.count}}</span>
						</div>
						<div class="lcarcontrol-wrapper">
							<carcontrol :food="food"></carcontrol><!-- 传入的food是上面li遍历时的food -->
						</div>
					</li>
				</ul>
			</div>
		</div><!-- 购物车点击详情页end -->
	</div>
</template>

<script>
import carcontrol from '../../components/carcontrol/carcontrol'

	export default{
		name:'shopcar',
		props:{
			selectFoods:{//这个数组是从goods组件里面的selectFoods方法传过来的，vue的数据驱动就是通过改变selectFoods的数据状态从而控制样式的变化，而原生js和其它的框架则需要通过判断条件来改变dom，可见vue更为轻便
				type:Array,
				default(){
					return [];
				}
			},
			deliveryPrice:{
				type:Number,
				default:0
			},
			minPrice:{
				type:Number,
				default:0
			}
		},
		data(){
			return{
				fold: true
			}
		},
		methods:{
			toggleList:function(){
					if(!this.totalCount){
						return;
					}
					this.fold = !this.fold;
				}
		},
		computed:{
			totalPrice:function(){
				let total = 0;
				this.selectFoods.forEach((food)=>{
					total += food.price * food.count;//food里面原来是没有count属性的，此属性是在carcontrol组件里面设置了，forEach(function(value),value),第一项参数必须，且需要传递参数，第二项是可选项
				});
				return total;
			},
			totalCount:function(){
					let count = 0;
					this.selectFoods.forEach((food)=>{
						count += food.count;
					});
					return count;
				},
			payDec:function(){
					if (this.totalPrice===0) {
						return `￥${this.minPrice}元起送`;
					}else if (this.totalPrice<this.minPrice) {
						let rest = this.minPrice - this.totalPrice;
						return `还差￥${rest}元起送`;
					}else {
						return `去结算`;
					}
				},
			payClass:function(){
					if (this.totalPrice<this.minPrice) {
						return `not-enough`;
					}else {
						return 'enough';
					}
				},
			listShow:function(){
					if (!this.totalCount) {
						this.fold = true; 
						return false;
					}
					let show = !this.fold;
					return show;
				}

		},
	components:{
			carcontrol
		}
	}
</script>

<style scoaped>
.shopcar{
	position: fixed;
	left: 0;
	bottom: 0;
	z-index: 50;
	width: 100%;
	height: 48px;
	background:#000;
}
.scontent{
	display: flex;
	background:#141d27;
}
.scontent-left{
	flex: 1
}
.scontent-right{
	flex: 0 0 105px;
	width: 105px;/*为了兼容安卓，必须写*/
	background:#2b333c;
}
.logo-wrapper{
	display: inline-block;
	vertical-align: top;
	position: relative;
	top: -10px;
	margin:0 12px;
	padding:6px;
	width: 56px;
	height: 56px;
	box-sizing: border-box;/*宽度包括了padding*/
	border-radius: 50%;/*圆形*/
	background:#141d27;
}
.logo{
	width: 100%;
	height: 100%;
	border-radius: 50%;/*圆形*/
	background:#2b343c;
	text-align: center;
}
.num{
	position: absolute;
	top: 0;
	right: 0;
	width: 24px;
	height: 16px;
	line-height: 16px;
	text-align: center;
	border-radius: 16px;
	font-size: 9px;
	font-weight: 400;
	color: #fff;
	background:rgb(240,20,20);
	box-shadow: 0 4px 8px 0 rgba(0,0,0,0.4);
}
.iconshopping_cart{
	font-size: 24px;
	line-height: 44px;
	color:#80858a;
}
.sprice{
	display: inline-block;
	vertical-align: top;/*实现居中*/
	line-height: 24px;/*为了使右边的线的高度不占满*/
	margin-top: 12px;
	padding-right: 12px;
	box-sizing: border-box;
	border-right: 1px solid rgba(255,255,255,0.1);
	font-size: 16px;
	font-weight: 700;
	color: rgba(255,255,255,0.4);
}
.sdec{
	display: inline-block;
	vertical-align: top;
	line-height: 24px;
	margin:12px 0 0 12px;
	font-size: 10px;
	color: rgba(255,255,255,0.4);
}
.pay{
	height: 48px;
	line-height: 48px;
	text-align: center;
	font-size: 12px;
	color: rgba(255,255,255,0.4);
	font-weight: 700;
}
.heighlight{
	background:rgb(0,160,220);
}
.textlight{
	color: #fff;
}
.pricelight{
	color: #fff;
}
.not-enough{
	background:#2b333b;
}
.enough{
	background:#00b43c;
	color: #fff;
}
.shopcar-list{
	position: absolute;/*相对于shaop-car绝对*/
	top: 0;
	z-index: -1;/*其在购物车组件的范围*/
	width: 100%;

}
.fold-transition{
	transition: all 0.5s;
	transform: translate3d(0,-100%,0);
}

</style>


