<template>
  <div id="sellers">
  	<div class="sellers-list">
  		<div class="list-wrapper">
		    <h1 class="stitle">{{seller.name}}</h1>
		    <div class="sdec-wrapper">
		    	<star :size="36" :score="seller.score"></star>
		    	<span class="srcount">({{seller.ratingCount}})</span>
		    	<span class="sscount">月售{{seller.sellCount}}单</span>
		    </div>
		    <div class="sdet-warpper">
		    	<div class="ssame">
		    		<h2 class="sstitle">起送价</h2>
		    		<span class="sprice">{{seller.minPrice}}<span class="ptext">元</span></span>
		    	</div>
		    	<div class="ssame">
		    		<h2 class="sstitle">商家配送</h2>
		    		<span class="sprice">{{seller.deliveryPrice}}<span class="ptext">元</span></span>
		    	</div>
		    	<div class="ssame">
		    		<h2 class="sstitle">平均配送时间</h2>
		    		<span class="sprice">{{seller.deliveryTime}}<span class="ptext">分钟</span></span>
		    	</div>
		    </div>
		</div>
	</div>
	<div class="sempty"></div>
	<div class="sellers-list">
		<div class="list-wrapper">
			<h3 class="stitle">公告与活动</h3>
			<div class="bcontent">{{seller.bulletin}}</div>
			<ul v-if="seller.supports">
				<li v-for="(item,index) in seller.supports" class="slist-wrapper">
					<span class="simg" :class="classMap[item.type]"></span>
					<span class="sdec">{{item.description}}</span>
				</li>
			</ul>
		</div>
	</div>
	<div class="sempty"></div>
	<div class="senvironment">
		<div class="environment-wrapper">
			<h2 class="stitle">商家实景</h2>
			<ul>
				<li class="eimg-list" v-for="pic in seller.pics">
					<span class="eimg" :src="pic"></span>
				</li>
			</ul>
		</div>
	</div>
	<div class="sempty"></div>
	<div class="sellers-info">
		<div class="list-wrapper">
			<h2 class="stitle">商家信息</h2>
			<ul>
				<li v-for="info in seller.infos" class="slist-wrapper s-info">
				<span class="sinfo">{{info}}</span>
				</li>
			</ul>
		</div>
	</div>
  </div>
</template>

<script>
import star from '../../components/Star/star'
import shopcar from '../../components/shopcar/shopcar'

export default {
  name: 'v-sellers',
  props:{
  	seller:{
  		type:Object
  	}
  },
  data() {
  	return {
  		//seller:{}
  	}
  },
  components:{
  	star,
  },
  created(){
  	this.classMap=['sdecrease-3','sdiscount-3','sguarantee-3','sinvoice-3','sspecial-3'];
  	//console.log(this.seller);
  	
  }

}
</script>

<style scoaped>
.sellers-list{
	width: 100%;
	border-bottom: 1px solid rgba(7,17,27,0.1);
}
.list-wrapper{
	margin: 18px 18px 0 18px;
}
.stitle{
	font-size: 14px;
	line-height: 14px;
	color: rgb(7,17,27);
	-webkit-margin-before: 0;
    -webkit-margin-after: 0;
}
.sdec-wrapper{
	margin-top: 8px;
	padding-bottom: 18px;
	border-bottom: 1px solid rgba(7,17,27,0.1);
	font-size: 0;
}
.star{
	display: inline-block;
	margin-right: 8px;
	vertical-align: top;
}
.srcount,.sscount{
	display: inline-block;
	font-size: 10px;
	line-height: 18px;
	color: rgb(77,85,93);
}
.sscount{
	padding-left: 12px;
}
.sdet-warpper{
	display: flex;
	margin:18px 0 18px 0;
	
}
.ssame{
	flex: 1;
	text-align: center;
	border-right: 1px solid rgba(7,17,27,0.1);
}
.ssame:last-child{
	border-right:none;
}
.sstitle{
	-webkit-margin-before: 0;
    -webkit-margin-after: 0;
    font-size: 10px;
    line-height: 10px;
    color: rgb(147,153,159);
}
.sprice{
	display: inline-block;
	margin-top: 4px;
	font-size: 24px;
	line-height: 24px;
	font-weight: 200;
}
.ptext{
	font-size: 10px;
	line-height: 24px;
}
.sempty{
	width: 100%;
	height: 16px;
	border-bottom: 1px solid rgba(7,17,27,0.1);
	background:#f3f5f7;
}
.bcontent{
	padding: 8px 12px 16px 12px;
	font-size: 12px;
	line-height: 24px;
	font-weight: 200;
	color: rgb(240,20,20);
	border-bottom: 1px solid rgba(7,17,27,0.1);
}
ul{
	margin: 0;
	padding: 0;
	list-style-type: none;
}
.slist-wrapper{
	padding:16px 12px;
	border-bottom: 1px solid rgba(7,17,27,0.1);
}
.slist-wrapper:last-child{
	border-bottom: none;
}
.simg{
	display: inline-block;
	width: 16px;
	height: 16px;
	background-repeat: no-repeat;
	background-size: 16px 16px;
	vertical-align: top;
}
.sdecrease-3{
	background-image: url('../Goods/decrease_3@2x.png');
}
.sdiscount-3{
	background-image: url('../Goods/discount_3@2x.png');
}
.sguarantee-3{
	background-image: url('../Goods/guarantee_3@2x.png');
}
.sinvoice-3{
	background-image: url('../Goods/invoice_3@2x.png');
}
.sspecial-3{
	background-image: url('../Goods/special_3@2x.png');
}
.sdec{
	font-size: 12px;
	font-weight: 200;
	color: rgb(7,17,27);
	line-height: 16px;
	padding-left: 6px;
	vertical-align: top;
}
.senvironment{
	width: 100%;
	border-bottom: 1px solid rgba(7,17,27,0.1);
}
.environment-wrapper{
	margin:18px 0 18px 18px;
	overflow: hidden;
}
.eimg-list{
	display: inline-block;
	width: 120px;
	height: 90px;
	padding:12px 6px 0 0;
}
.eimg{
	width: 120px;
	height: 90px;
}
.sinfo{
	font-size: 12px;
	font-weight: 200;
	color: rgb(7,17,27);
	line-height: 16px;
}
.s-info:first-child{
	margin-top: 12px;
	border-top: 1px solid rgba(7,17,27,0.1);
}
</style>



