<template>
  <div id="ratings">
    <div class="r-goal">
    	<div class="goal-wrapper">
    		<div class="r-goals">
    			<div class="goals">{{seller.score}}</div>
    			<div class="tgoals">综合评分</div>
    			<div class="hgoals">高于周边商家{{seller.rankRate}}%</div>
    		</div>
    		<div class="r-atitude">
    			<div>
    				<span class="t-atitude">服务态度</span>
    				<star :size="36" :score="seller.serviceScore"></star>
    				<span class="s-atitude">{{seller.score}}</span>
    			</div>
    			<div class="atitude">
    				<span class="t-atitude">服务态度</span>
    				<star :size="36" :score="seller.serviceScore"></star>
    				<span class="s-atitude">{{seller.score}}</span>
    			</div>
    			<div class="rtime">
    				<span class="t-rtime">送达时间</span>
    				<span class="r-rtime">{{seller.deliveryTime}}分钟</span>
    			</div>
    		</div>
    	</div>
    </div>
    <div class="sempty"></div>
    <div class="customer-ratings">
    	<div class="select-button">
        <!-- <span id="userName">uraydu4516668341</span>
    		<button class="r-all" @click="copyText">全部</button> -->
         <span>全部{{ratings.length}}</span>
         <span>满意{{satisfy}}</span>
         <span>不满意{{unsatisfy}}</span>
    	</div>
      <div class="select-filter" @click=selectHave>
        <span class="iconcheck_circle"></span><span>只看有内容的评价</span>
      </div>
    </div>
    <div class="select-lists" >
      <ul>
        <li v-for='rating in ratings' class="lis">
          <div class="all-lists">
            <div class="avatar">
              <img :src="rating.avatar" width='28px' height="28px">
            </div>
            <div class="content-list">
              <div class="ratetime">{{rating.rateTime}}</div>
              <div class="username">{{rating.username}}</div>
              <star :size="24" :score="rating.score"></star>
              <span class="s-delivery" v-show='rating.deliveryTime'>{{rating.deliveryTime}}分钟送达</span>
              <div class="s-text">{{rating.text}}</div>
              <i class="iconthumb_up"></i>
              <span class="s-recommend" v-for='recommend in rating.recommend'>
                <span>{{recommend}}</span>
              </span>
            </div>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import star from '../../components/Star/star'


export default {
  name: 'v-ratings',
  props:{
  	ratings:{
  		type:Array
  	},
  	seller:{
  		type:Object
  	}
  },

  data() {
    return{
      satisfy: 0,
      unsatisfy:0,
      text:[]
    }
  },

  methods:{
    /*copyText:function(){//复制DOM文本
     var targetText = document.getElementById("userName");
     var range = document.createRange();//返回一个Range对象，用来创建选中容器
     range.selectNode(targetText);//用来设定一个包含节点和节点内容的Range
     window.getSelection().addRange(range);//返回一个selection对象，表示用户选择的文本，并将一个区域range对象加入选区
     document.execCommand('copy');//执行复制命令
     window.getSelection().removeAllRanges();//从选区中移除一个区域/将所有的区域都从选中区中移除
  }*/
    selectHave:function () {
      let aLi = document.getElementsByClassName('lis');
      let iconcheck = document.getElementsByClassName('iconcheck_circle');

      iconcheck[0].style.color = 'rgb(0,160,220)';
      for (let i = 0; i < aLi.length; i++) {
          if (this.text[i] === '') {
              aLi[i].style.display = 'none';
          }
          else{
             aLi[i].style.display = 'block';
          }
       }
    }


  },

  mounted() {
        for (let i = 0; i < this.ratings.length; i++) {
          if (this.ratings[i].score >= 3) {
              this.satisfy++;
          }
          else{
             this.unsatisfy++;
          }
          this.text.push(this.ratings[i].text);
       }
  },


  components:{
  	star
  }
}
</script>

<style scoaped>
@import "../../assets/common/style.css";

#ratings{
	 position: absolute;
     top: 174px;
     bottom: 0;
     left: 0;
     width: 100%;
     overflow-x: hidden;
}
.r-goal{
	border-bottom: 1px solid rgba(7,17,27,0.1);
}
.goal-wrapper{
	display: flex;
	margin: 18px 0;
}
.r-goals{
	flex: 0 0 137px;
	width: 137px;
	text-align: center;
	border-right: 1px solid rgba(7,17,27,0.1);
}
.goals{
	font-size: 24px;
	color: rgb(255,153,0);
	line-height: 28px;
}
.tgoals{
	font-size: 12px;
	color: rgb(7,17,27);
	line-height: 12px;
	padding-top: 6px;
}
.hgoals{
	font-size: 10px;
	color: rgb(147,153,159);
	line-height: 10px;
	padding-top: 8px;
}
.r-atitude{
	flex: 1;
	padding-left: 24px;
}
.t-atitude{
	font-size: 12px;
	color: rgb(7,17,27);
	line-height: 18px;
	padding-right: 12px;
	vertical-align: top;

	
}
.star{
	display: inline-block;
	vertical-align: top;
}
.s-atitude{
	font-size: 12px;
	color: rgb(255,153,0);
	line-height: 18px;
	padding: 0 24px 0 12px;
}
.atitude{
	margin-top: 8px;
}
.rtime{
	margin-top: 8px;
}
.t-rtime{
	font-size: 12px;
	color: rgb(7,17,27);
	line-height: 18px;
	padding-right: 12px;
}
.r-rtime{
	font-size: 12px;
	color: rgb(147,153,159);
	line-height: 18px;
}
.sempty{
	width: 100%;
	height: 16px;
	border-bottom: 1px solid rgba(7,17,27,0.1);
	background:#f3f5f7;
}
.customer-ratings{
  width: 100%;
  height: 100px;
  border-bottom: 1px solid rgba(7,17,27,0.1);
}
.select-button{
  width: 80%;
  margin:  18px 0px 0px 26px;
  padding-bottom: 18px;
  border-bottom: 1px solid rgba(7,17,27,0.1);
}
.select-button span{
  display: inline-block;
  line-height: 30px;
  width: 60px;
  height: 30px;
  background: #f0f8ff;
  text-align: center;
  font-size: 12px;
}
.select-button span:first-child{
  background: rgb(0,160,220);
}
.select-button span:last-child{
  background: gray;
}
.select-filter{
  width: 50%;
  margin: 18px 0px 18px 26px;
}
.select-filter span{
  font-size: 10px;
  line-height: 10px;
  color:rgb(147,153,159);
}
.select-lists ul{
  list-style-type: none;
  margin: 0px;
  padding: 0px;
}
.all-lists{
  width: 90%;
  display: flex;
  border-bottom: 1px solid rgba(7,17,27,0.1);
  margin-left: 18px;
}
.ratetime{
  position: absolute;
  right: 18px;
  font-size:10px;
  font-weight: 200;
  color: rgb(147,153,159);
  line-height: 12px;
}
.all-lists .avatar{
  flex: 0 0 28px;
  width: 28px;
  height: 28px;
  margin: 18px 12px 0px 0px;
}
.all-lists .avatar img{
  border-radius: 50%;
}
.content-list{
 flex: 1;
 margin: 18px 18px 18px 0px;
}
.username{
  font-size: 10px;
  color: rgb(7,17,27);
  line-height: 12px;
}
.s-delivery{
  font-size: 10px;
  color: rgb(147,153,159);
  margin-left: 6px;
  vertical-align: top;
}
.s-text{
  font-size: 12px;
  color: rgb(7,17,27);
  line-height: 18px;
}
.iconthumb_up{
  font-size: 12px;
  color: rgb(0,160,220);
  line-height: 18px;
  margin-top: 6px;
}
.iconthumb_down{
  font-size: 12px;
  color: rgb(183,187,191);
  line-height: 18px;
  margin-top: 6px;
}
.s-recommend{
  margin-top: 8px;
  margin-left: 8px;
  padding-left:6px;
  padding-right: 6px;
  font-size: 9px;
  color: rgb(147,153,159);
  line-height: 16px;
  width: 40px;
  height: 20px;
  background-color: rgb(255,255,255);
  border: 1px solid rgba(7,17,27,0.1);
  border-radius: 2px;
}
</style>



