<template>
  <div id="app">
    <!-- <img src="./assets/logo.png">
    <HelloWorld/> -->
    <v-header :seller="seller"></v-header>
    <div class="tab">
      <router-link to="/"><div class="tab-items">商品</div></router-link>
      <router-link to="/sellers"><div class="tab-items">商家</div></router-link>
      <router-link to="/ratings"><div class="tab-items">评价</div></router-link>
    </div>
    <!-- <router-view :goods="goods" :seller="seller"></router-view> -->
    <router-view :seller="seller" :ratings="ratings"></router-view>
  </div>
</template>

<script>
// import HelloWorld from './components/HelloWorld'
import header from './components/Header/header'


export default {
  name: 'App',
  components: {
    // HelloWorld
    "v-header":header
  },
  data(){
    return{
      seller:{},
      // goods:[],
      ratings:[]
    }
  },
  mounted(){
    var _this = this;
    this.$http.get('./static/data.json').then(function(res){
        _this.seller = res.data.seller;
        //console.log(_this.seller);
        // _this.goods = res.data.goods;
        // console.log(_this.goods);
        _this.ratings = res.data.ratings;

    }).catch(function (err) {
      console.log(err);
    });
  }
}
</script>

<style>

body,html{
    margin: 0px;
    padding:0px;
    font-weight:200;
    line-height:1;
    font-family:arial,sans-serif;
}
.tab{
  display:flex;
  width:100%;
  height:40px;
  line-height: 40px;
  border-bottom: 1px solid rgba(7,17,27,0.1);
}
.tab-items{
  flex-grow: 1;
  text-align: center;
}
a{
  text-decoration: none;
  -webkit-tap-highlight-color:transparent;
  tap-highlight-color:transparent;
  display: block;
  flex-grow: 1;
  font-size: 14px;
  color: rgb(77,85,93);
}
a:hover{
  color: rgb(240,20,20);
}
.clearfix{/*通用clearfix写法*/
  display: inline-block;
}
.clearfix:after{
  display: block;
  content: ".";
  height: 0;
  line-height: 0;
  clear: both;
  visibility: hidden;
}

</style>



