<template>
  <div id="goods">
    <div class="menu-wrapper" ref="menuWrapper"><!-- 在使用better-scroll时，ref进行渲染dom结构 -->
    	<ul>
    		<li v-for="(item,index) in goods" class="menu-item" :class="{'current': currentIndex==index}" @click="selectMenu(index,$event)"><!-- 当滚动时会产生index,如果它与currentIndex返回值相等，则应用current样式,cuurentIndex是foods里面的li项目的index，而与之比较的index则是menu里面li项目的index值,click事件是为了使左边的菜单被点击时，其所对应的内容能够相应的被显示-->
    			<span class="text">
    				<span v-show="item.type>0" class="icon" :class="classMap[item.type]"></span>{{item.name}}
    			</span>
    		</li>
    	</ul>
    </div>
    <div class="foods-wrapper" ref="foodsWrapper">
    	<ul>
    		<li v-for="item in goods" class="food-list food-list-hook"><!-- 定义food-list-hook来进行js选择操作，但是实际上它本身并不存在具体的样式 -->
    			<h1 class="ftitle">{{item.name}}</h1>
    			<ul>
    				<li v-for="food in item.foods" class="food-item">
    					<div class="ficon">
    						<img width="57" height="57" :src="food.icon">
    					</div>
    					<div class="fcontent">
    						<h2 class="fname">{{food.name}}</h2>
    						<p class="fdesc">{{food.description}}</p>
    						<div class="fextra">
    							<span class="fcount">月售{{food.sellCount}}份</span><span>好评率{{food.rating}}%</span>
    						</div>
    						<div class="fprice">
    							<span class="fnow">￥{{food.price}}</span><span class="fold" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
    						</div>
    						<div class="car-wrapper">
    							<carcontrol :food="food"></carcontrol><!-- 传入的food就是遍历li的foods时的food对象 -->
    						</div>
    					</div>
    				</li>
    			</ul>
    		</li>
    	</ul>
    </div>
    <shopcar :selectFoods="selectFoods" :deliveryPrice="seller.deliveryPrice" :minPrice="seller.minPrice"></shopcar>
    <!-- 传的是selectFoods函数的返回数组 -->
  </div>
</template>

<script type="text/javascript">
import BScroll from 'better-scroll'/*安装better-scroll,并引入相关文件，为了使菜单栏目和内容栏目可以进行局部滚动*/

import shopcar from '../../components/shopcar/shopcar'
import carcontrol from '../../components/carcontrol/carcontrol'

export default {
  name: 'v-goods',
  props:{
  		seller:{
  			type:Object
  		},
  		// goods:{
  		// 	type:Array
  		// }
  },
  data(){
  	return{
  		listHeight:[],//存放每个区间的高度
  		scrollY: 0,//实时获取高度
  		goods:[]
  	}
  },
  methods:{
  	initScroll:function(){
  		this.menuScroll = new BScroll(this.$refs.menuWrapper,{
  			click:true, //使better-scroll的点击事件生效

  		});/*使用better-scroll只需要进行new一个实例化对象，其参数有两个，一个是利用$refs调用dom实例对象，另一个是接受一个json对象，参考网址：https://www.imooc.com/article/18232*/

  		this.foodsScroll = new BScroll(this.$refs.foodsWrapper,{
  			probeType:3,//让scrollY能实时被监测,probeType: 1：滚动的时候会派发scroll事件，会截流。2：滚动的时候实时派发scroll事件，不会截流。 3：除了实时派发scroll事件，在swipe的情况下仍然能实时派发scroll事件(截流就是在滚动的过程中,不要实时派发,这样会让派发的 scorll事件变少。如果不截流的话,在整个滚动过程中会实时派发 scroll事件)
  			click:true,

  		});

  		this.foodsScroll.on('scroll',(pos)=>{
  			this.scrollY = Math.abs(Math.round(pos.y));//此事件在滚动时可以实时监测Y值，取正值，取整
  		});
  	},
  	calculateHeight:function(){
  		let food_list = document.getElementsByClassName("food-list-hook");//获取所有的带有.food-list-hook的li元素
  		let height = 0;
  		this.listHeight.push(height); 
  		for(let i=0;i<food_list.length;i++){
  			let item = food_list[i];//获取每个带有.food-list-hook的li元素
  			height += item.clientHeight;//获取每个li元素的高度
  			this.listHeight.push(height);//将获取到的高度保存到listHeight数组中
  		}
  	},
  	selectMenu:function(index,event){//点击左侧菜单时，右侧内容能够对应的显示
  		// console.log(index);
  		if (!event._constructed) {
  			return;//防止better-scroll的event事件不一致
  		}
  		let food_list = document.getElementsByClassName("food-list-hook");
  		let el = food_list[index];
  		this.foodsScroll.scrollToElement(el,300);//使用此方法可以让右侧的内容滚动到相应的位置
  	}
  },
  computed:{
  		currentIndex(){
  			for(let i=0;i<this.listHeight.length;i++){
  				let height1 = this.listHeight[i];
  				let height2 = this.listHeight[i+1];
  				if(!height2 ||(this.scrollY>=height1&&this.scrollY<height2)){
  					return i;
  				}
  			}
  			return 0;
  		},
  		selectFoods(){
  			let foods = [];
  			this.goods.forEach((good)=>{
  				good.foods.forEach((food)=>{
  					if(food.count){
  						foods.push(food);
  					}
  				});
  			});
  			return foods;
  		}
  },
  created(){
  	var _this = this;
  	this.classMap = ['decrease-3','discount-3','special-3','invoice-3','guarantee-3'];
  	this.$http.get('./static/data.json').then(function(res){
  
       _this.goods = res.data.goods;
        // console.log(_this.goods);

    }).catch(function (err) {
      console.log(err);
    });
  	this.$nextTick(() => {
  		this.initScroll();/*在调用初始化的better-scroll方法时，要与$nextTick回调函数结合使用*/
  		this.calculateHeight();/*在调用初始化的better-scroll方法时，要与$nextTick回调函数结合使用*/
  		
  	});

  },
  components:{
  	shopcar,carcontrol
  }

}
</script>

<style scoaped>
#goods{
	position: absolute;
	width: 100%;
	top: 174px;/*绝对定位的top是根据Goods、tab的高度计算的*/
	bottom: 46px;/*绝对定位的bottom是根据页面下购物车的高度计算的*/
	display: flex;/*采用flex布局*/
	overflow: hidden;/*内容溢出是隐藏*/
}
.menu-wrapper{
	flex:  0 0 80px;/*第一个是等分，二是内容的缩放，三是其占用空间*/
	width: 80px;/*安卓兼容问题，必须写*/
	background: #f3f5f7;
}
.foods-wrapper{
	flex: 1;
}
ul{
	list-style-type: none;
	padding: 0;
	margin:0;
}
.menu-item{
	display: table;/*多行垂直居中*/
	height: 54px;
	width: 56px;
	line-height: 14px;
	padding: 0 12px;
}
.menu-item .icon{
  display: inline-block;
  width: 12px;
  height: 12px;
  margin-right: 2px;
  background-size: 12px 12px;
  background-repeat: no-repeat;
  vertical-align: top;
  /*background-image: url("../Goods/decrease_3@2x.png");*/
}
.decrease-3{
  background-image: url("../Goods/decrease_3@2x.png");
}
.discount-3{
  background-image: url("../Goods/discount_3@2x.png");
}
.special-3{
  background-image: url("../Goods/special_3@2x.png");
}
.invoice-3{
  background-image: url("../Goods/invoice_3@2x.png");
}
.guarantee-3{
  background-image: url("../Goods/guarantee_3@2x.png");
}
.menu-item .text{
	font-size: 12px;
	display: table-cell;
	width: 56px;
	vertical-align: middle;
	border-bottom: 1px solid rgba(7,17,27,0.1);
}
.ftitle{
	font-size: 12px;
	color: rgb(147,153,159);
	height: 26px;
	line-height: 26px;
	padding-left: 14px;
	border-left: 2px solid #d9dde1;
	background:#f3f5f7;
	margin: 0;
}
.food-item{
	display: flex;
	margin: 18px;
	padding-bottom: 18px;/*上下margin会重合*/
	border-bottom: 1px solid rgba(7,17,27,0.1);
}
.food-item:last-child{
	border-bottom: none;
	margin-bottom: 0;
}
.ficon{
	flex:  0 0 57px;
	margin-right: 10px;
}
.fcontent{
	flex: 1;
	position: relative;
}
.fname{
	margin: 2px 0 8px 0;
	height: 14px;
	line-height: 14px;
	font-size: 14px;
	color: rgb(7,17,27);
}
.fdesc,.fextra{
	line-height: 10px;
	font-size: 10px;
	color: rgb(147,153,159);
	-webkit-margin-before: 0;/*下面两行为浏览器兼容*/
    -webkit-margin-after: 0;

}
.fdesc{
	margin-bottom: 8px;
	line-height: 12px;
}
.fcount{
	margin-right: 12px;
}
.fprice{
	font-weight: 700;
	line-height: 24px;
}
.fnow{
	margin-right: 8px;
	font-size: 14px;
	color: rgb(240,20,20);
}
.fold{
	text-decoration:line-through;
	font-size: 10px;
	color: rgb(147,153,159);
}
.current{
	position: relative;
	background:#fff;
	font-weight: 700;
	margin-top:-1px;
	z-index:10;
}
.current .text{
	border:none;
}
.car-wrapper{
	position: absolute;
	right: 2px;
	bottom: 1px;
}

</style>



