<template>
  <div id="header">
      <div class="content-wrapper">
        <div class="avatar">
           <img width="64" height="64" :src="seller.avatar">
        </div>
        <div class="content">
             <div class="content-title">
               <span class="brand"></span>
               <span class="name">{{seller.name}}</span>
             </div>
             <div class="description">
               {{seller.description}}/{{seller.deliveryTime}}分钟送达
             </div>
             <div v-if="seller.supports" class="support">
               <span class="icon" :class="classMap[seller.supports[1].type]"></span>
               <span class="text">{{seller.supports[0].description}}</span>
             </div>
             <div v-if="seller.supports" class="support-count" @click="showDetail">
               <span class="count">{{seller.supports.length}}个</span>
               <i class="iconkeyboard_arrow_right"></i>
             </div>
        </div>
      </div>
      <div class="bulletin-wrapper" @click="showDetail">
        <span class="bulletin-title"></span><span class="bulletin-text">{{seller.bulletin}}</span><!-- 为了避免出现空格，让span标签紧贴 -->
        <i class="iconkeyboard_arrow_right"></i>
      </div>
      <div class="background">
        <img :src="seller.avatar" width="100%" height="100%"><!-- header的背景为一张图片加滤镜的组合 -->
      </div>
      <div v-show="detialshow" class="detail">
        <div class="detail-wrapper clearfix">
          <div class="detail-main">
            <h1 class="dname">{{seller.name}}</h1>
            <div class="star-wrapper">
              <star :size="48" :score="seller.score"></star>
            </div>
            <!-- flex布局 -->
            <div class="detail-title">
              <div class="dline"></div>
              <div class="dtext">优惠信息</div>
              <div class="dline"></div>
            </div>
            <ul v-if="seller.supports" class="detail-support">
              <li v-for="(item,index) in seller.supports">
                  <span :class="classMap[seller.supports[index].type]" class="dicon"></span>
                  <span class="dtext">{{seller.supports[index].description}}</span>
              </li>
            </ul>
            <div class="detail-info">
              <div class="inline"></div>
              <div class="intext">商家公告</div>
              <div class="inline"></div>
            </div>
            <div class="detail-bulletin">
              <p>{{seller.bulletin}}</p>
            </div>
          </div>
        </div>
        <div class="detail-close" @click="showDetail"><!-- 点击关闭符号时，关闭弹层 -->
          <i class="iconclose"></i>
        </div>
      </div><!-- 点击出现弹层 ，当点击.support-count,.bulletin-wrapper时，触发对应方法-->
  </div>
</template>

<script>
import star from '../../components/Star/star'

export default {
  name: 'v-header',
  props:{
    seller:{
      type: Object 
    } 
  },
  data(){
    return{
        detialshow:false,
    }
  },
  components:{
    star
  },
  methods:{
    showDetail:function(){
      this.detialshow = !this.detialshow;
    }
  },
  created(){
    this.classMap = ['decrease','discount','special','invoice','guarantee'];
    /*实现样式映射关系，当.icon所动态绑定的样式为数组中的相应字段时，则此时将绑定样式中与数组字段相对应的样式，实现的动态样式变换*/
  }
  
 } 
</script>

<style scoaped>
/*使用图标字体文件时应先引用图标字体文件的样式*/
@import "../../assets/common/style.css";
#header{
  position: relative;
	width:100%;
  color:#fff;
  background:rgba(7,17,27,0.5);/*背景色与滤镜达到设计灰色模糊的效果*/
  overflow: hidden;/*将背景的溢出部分隐藏*/
}
.content-wrapper{
  position: relative;
  padding:24px 24px 18px 12px;
  font-size: 0;
}
.avatar{
  display: inline-block;
  vertical-align: top;/*让两个div顶部对齐*/
}
.avatar img{
  border-radius:2px; 
}
.content{
  display: inline-block;
  margin-left: 16px;
}
.support-count{
  position: absolute;/*其相对于content-wrapper进行绝对定位*/
  right: 12px;
  bottom: 14px;
  padding:0 8px;
  height: 24px;
  line-height: 24px;
  border-radius: 14px;
  background:rgba(0,0,0,0.2);
  text-align: center;
}
.count{
  vertical-align: top;
  font-size: 10px;
}
.support-count .iconkeyboard_arrow_right{
  line-height: 24px;
  margin-left: 2px;
  font-size: 10px;
}
.content-title{
  margin: 2px 0 8px 0;
}
.brand{
  display: inline-block;
  width:30px;
  height: 18px;
  background-image: url("../Header/brand@2x.png");
  background-size: 30px 18px;
  background-repeat: no-repeat;
  vertical-align:top;/*让图标与文字顶部对齐*/
}
@media (-webkit-min-device-pixel-ratio:3),(min-device-pixel-ratio:3){
  .brand{
    background-image: url("../Header/brand@3x.png");
  }
  .decrease{
    background-image: url("../Header/decrease_1@3x.png");
  }
  .discount{
    background-image: url("../Header/discount_1@3x.png");
  }
  .special{
    background-image: url("../Header/special_1@3x.png");
  }
  .invoice{
    background-image: url("../Header/invoice_1@3x.png");
  }
  .guarantee{
    background-image: url("../Header/guarantee_1@3x.png");
  }
  .bulletin-title{
    background-image: url("../Header/bulletin@3x.png");
  }
}
.name{
  margin-left: 6px;
  font-size: 16px;
  line-height: 18px;
  font-weight: bold;
}
.description{
  margin-bottom: 10px;
  line-height: 12px;
  font-size: 12px
}
.icon{
  display: inline-block;
  width: 12px;
  height: 12px;
  margin-right: 4px;
  background-size: 12px 12px;
  background-repeat: no-repeat;
  vertical-align: top;
  /*background-image: url("../Header/decrease_1@2x.png");*/
}
.decrease{
  background-image: url("../Header/decrease_1@2x.png");
}
.discount{
  background-image: url("../Header/discount_1@2x.png");
}
.special{
  background-image: url("../Header/special_1@2x.png");
}
.invoice{
  background-image: url("../Header/invoice_1@2x.png");
}
.guarantee{
  background-image: url("../Header/guarantee_1@2x.png");
}
.text{
  font-size: 10px;
  line-height: 12px;
}
.bulletin-wrapper{
  position: relative;
  height: 28px;
  line-height: 28px;
  padding: 0 22px 0 12px;
  white-space: nowrap;/*以下三行代码可让字体超出容器时显示"..."*/
  overflow: hidden;
  text-overflow: ellipsis;/*end*/
  background:rgba(7,17,27,0.2);
}
.bulletin-title{
  display: inline-block;
  vertical-align: top;
  margin-top: 8px;
  width: 22px;
  height: 12px;
  background-image: url("../Header/bulletin@2x.png");
  background-size: 22px 12px;
  background-repeat: no-repeat;
}
.bulletin-text{
  vertical-align:top;
  font-size: 10px;
  margin: 0 4px;
}
.bulletin-wrapper .iconkeyboard_arrow_right{
  position: absolute;/*这个图标不占用空间，使用绝对定位，相对于父元素定位*/
  font-size: 10px;
  right: 12px;
  top: 8px;
}
.background{
  position: absolute;/*其相对于header进行绝对定位*/
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;/*让背景图显示在底层*/
  filter: blur(10px);/*设置滤镜达到模糊效果*/
}
.detail{/*弹层*/
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: auto;/*为了内容溢出时仍能显示*/
  background:rgba(7,17,27,0.8);
 /* backdrop-filter:blur(10px);浏览器不支持此写法*/
}
.detail-wrapper{/*使显示的内容的高度为手机屏幕的高度*/
  min-height: 100%;
  width: 100%;
}
.detail-main{
  margin-top: 64px;
  padding-bottom: 64px;/*此处不能用margin因为关闭符号占空间,并且当内容超过屏幕高度时，使关闭图片不被内容文字覆盖，给图片留了空间*/
}
.dname{
  line-height: 26px;
  text-align: center;
  font-size: 16px;
  font-weight: 700;
}
.detail-close{
  position: relative;
  width: 32px;
  height: 32px;
  margin: -64px auto 0 auto;/*-64的margin是为了让图片向上移动*/
  clear: both;
  font-size:32px;
}
.star-wrapper{
  height: 24px;
  margin: 16px 0 28px 0;
  text-align: center;
}
.detail-title{
  display: -webkit-flex;
  display: flex;
  margin: 0 36px 0 36px;
}
.dline{
  flex: 1;
  width: 112px;
  border-bottom: 2px solid rgba(255,255,255,0.2);
  position: relative;/*最后两行代码是让line能够垂直居中显示*/
  top: -6px;
}
.dtext{
  font-size: 14px;
  font-weight: 700;
  color: rgb(255,255,255);
  line-height: 14px;
  margin: 0 12px 0 12px;
}
.detail-support{
  list-style-type: none;
  margin: 0;
  padding:0;
  margin:24px 0 28px 36px;
}
.dicon{
  display: inline-block;
  width: 16px;
  height: 16px;
  background-size: 16px 16px;
  background-repeat: no-repeat;
  margin:0  0 12px 12px;
  vertical-align: top;/*使两个span(dicon、dtext)对齐*/
}
.dtext{
  font-size: 12px;
  font-weight: 200;
  line-height: 16px;
  margin-left: 6px;
  vertical-align: top;/*使两个span对齐*/
}
.detail-info{
  display: -webkit-flex;
  display: flex;
  margin: 0 36px 0 36px;
}
.inline{
  flex: 1;
  width: 112px;
  border-bottom: 2px solid rgba(255,255,255,0.2);
  position: relative;/*最后两行代码是让line能够垂直居中显示*/
  top: -6px;
}
.intext{
  font-size: 14px;
  font-weight: 700;
  color: rgb(255,255,255);
  line-height: 14px;
  margin: 0 12px 0 12px;
}
.detail-bulletin{
  width: 80%;
  margin:24px 36px 0 36px;

}
.detail-bulletin p{
  font-size: 12px;
  font-weight: 200;
  line-height: 24px;
  padding: 0 12px;
}
</style>


